import React from 'react'
import { connect } from 'react-redux'
import { Message, Button, Icon } from 'semantic-ui-react'
import { dataTypes } from './services/OrdonnanceActions';
import ComponentLoader from '../globals/util-components/ComponentLoader';

function mapStateToProps(state) {
	return {
		ordonnance: state.ordonnance[dataTypes.ORDONNANCE_EMISE],
	}
}

function OrdonnanceConfirm({ ordonnance, ...others }) {
	return (
		<ComponentLoader loadedObject={ordonnance}>
			{ordonnance && ordonnance.fileURL ?
				<Message info>
					<p>Votre ordonnance a bien t enregistre.</p>
					<p>Si elle ne s'affiche pas automatiquement, vous pouvez y accder en cliquant le lien suivant</p>
					<a id='ordo' title='ordonnance mise' href={ordonnance.fileURL} target='_blank' rel='noopener noreferrer'>
						<Button primary icon labelPosition='left' size='small'>
							<Icon name='file outline' />
							Editer l'ordonnance
						</Button>
					</a>
				</Message>
				:
				<Message error>
					Un problme est survenu : l'ordonnance n'a pas pu tre mise. Veuillez recommencer ultrieurement
				</Message>
			}
		</ComponentLoader>


	)
}

export default connect(mapStateToProps)(OrdonnanceConfirm)

