import React, { Fragment } from "react";
import { Field } from "redux-form";
import { Form, Message } from "semantic-ui-react";
import * as formAdapter from "../globals/redux/reduxFormAdapter"
import { Grid } from "@material-ui/core";
import { BoutonAjouter, BoutonSupprimer } from "../globals/util-components/Boutons";

export default function PrescriptionLibreForm({ numMedicaments, handleClickAjouter, handleClickSupprimer }) {
	return (
		<Fragment> {
			Array.isArray(numMedicaments) && numMedicaments.map((numMedicament, index) => {
				return (
					<Fragment key={index}>
						{(index === 0) ?
							<Message info>Vous pouvez saisir librement ci-dessous les mdicaments  ajouter  votre prescription </Message> : ''
						}
						<LigneDispensation numMedicament={numMedicament} handleClickSupprimer={handleClickSupprimer} />
					</Fragment>
				)
			})}
			<div style={{ paddingTop: '1rem' }}>
				<BoutonAjouter title='Ajouter un mdicament' handleClick={handleClickAjouter} />
			</div>
		</Fragment>
	)
}

const LigneDispensation = function ({ numMedicament, handleClickSupprimer }) {
	return (
		<Grid container spacing={1} >
			<Grid item xs={4}>
				<Field component={Form.Input} label='Dsignation' name={'medicament' + numMedicament} placeholder='mdicament' />
			</Grid>
			<Grid item xs={3}>
				<Field component={Form.Input} label='Dosage' name={'dosage' + numMedicament + 0} placeholder='dosage' />
			</Grid>
			<Grid item xs={3}>
				<Field component={Form.Input} label="Qt/prise" name={'quantite' + numMedicament} placeholder="Nb comprims" />
			</Grid>
			<Grid item xs={3}>
				<Field component={formAdapter.FormSelect} label="Forme" name={'forme' + numMedicament} placeholder="Comprim ..."
					options={[
						{ key: "forme1", text: "Comprim", value: "comprim(s)" },
						{ key: "forme2", text: "Glule", value: "glule(s)" }
					]}
				/>
			</Grid>
			<Grid item xs={4}>
				<Field component={Form.Input} label="Frquence" name={'frequence' + numMedicament} placeholder="Nb fois / jour" />
			</Grid>
			<Grid item xs={3}>
				<Field component={Form.Input} label="Dure" name={'duree' + numMedicament} placeholder="Nb jours" />
			</Grid>
			<Grid item xs={1}>
				<div style={{ marginLeft: '0.5rem', marginTop: '50%' }} >
					<BoutonSupprimer index={numMedicament} handleClick={handleClickSupprimer} title='Supprimer le mdicament' />
				</div>
			</Grid>
		</Grid>
	)
}


