import React, { createContext } from 'react';
import { Form } from 'semantic-ui-react';

import FormHandler from '../globals/hoc/FormHandler';
import { formNames } from '../globals/redux/FormActions'
import OrdonnanceTypeForm from './OrdonnanceTypeForm';


// Config gnrale pour ! TESTS ! //
const listeProduits = [
	{ id: 1, designation: "Paractamol" },
	{ id: 2, designation: 'Tramadol' },
	{ id: 3, designation: 'Codine' },
	{ id: 4, designation: 'Poudre d\'opium' }
]
const listeUnitesDosage = ['g', 'mg', 'mg LI', 'mg LP', 'g/h']
const formesMedicamenteuses = ['Comprim', 'Glule', 'Suppositoire', 'Goutte', 'Flasque']

// Contexte portant la config gnrale de la fonctionnalit
export const NomenclatureContext = createContext({ listeProduits, listeUnitesDosage, formesMedicamenteuses })

// Composant charg du rendu 
export default function GestionNomenclature(props) {

	const Formulaire = props =>
		<Form size='tiny' style={{ marginBottom: '1rem' }}>
			<OrdonnanceTypeForm {...props} />
		</Form>

	return (
		<FormHandler component={Formulaire}
			formName={formNames.CONFIG_ORDONNANCE_FORM}
			mode='Saisie'
			{...props}
		/>
	)
}


